import React from 'react'
import classes from './GoodsComp.module.css'

const GoodsComp = (props) => {

     
    const assignedClasses = [classes.image];
    if (props.image === 'image1.jpg') {
      assignedClasses.push(classes.image1);
    }
    if (props.image === 'image2.png') {
      assignedClasses.push(classes.image2);
    }

    let ifActive = null
    if (props.isSelectable) {
        ifActive = (<input style={{marginRight: '8px'}} id={props.id} type="checkbox" value={props.id}></input>)

        if (props.selectAll) { 
            ifActive = (
                <input id={props.id} style={{marginRight: '8px'}} type="checkbox" value={props.id} checked></input>
            )
            console.log("stateChanges")
        }  
    }
    

    
    return (


        <label for={props.id} onChange={props.clicked} className={classes.GoodsCompItem }>
        <div>
            <h1 >{props.title}</h1>
            <h3 className={classes.listItemH3}>{props.desc}</h3>
            <div className={classes.Group}>
                {ifActive}
                <div className={assignedClasses.join(' ')}></div>
                <h2>{props.price}</h2>
            </div>
            <div className={classes.Group}>
                <div>
                    <h4>{props.howOld}</h4>
                    <h4>{props.city}</h4>
                </div>
                <i className={classes.eye}></i><span>{props.viewCount}</span>    
            </div>
        </div>
        </label>
    )    
};

export default GoodsComp;  