import React from 'react'
import classes from './MenuElement.module.css'


const MenuElement = (props) => {

    const assignedClasses = [];
    if (props.icon === 'Refresh') {
      assignedClasses.push(classes.Refresh);
    }
    if (props.icon === 'Trash') {
      assignedClasses.push(classes.Trash);
    }
    if (props.icon === 'Pay') {
        assignedClasses.push(classes.Pay);
      }
      if (props.icon === 'Uplift') {
        assignedClasses.push(classes.Uplift);
      }
      if (props.icon === 'Stick') {
        assignedClasses.push(classes.Stick);
      }

    let menuIcon = null;
    if (props.icon) {
        menuIcon = <i className={assignedClasses}></i>
    }


    return (      
        <div className={classes.MenuElementContainer} onClick={props.clicked}>
            {menuIcon}
            <span className={classes.MenuText}>{props.title}</span>
        </div>
       
     )    

}

export default MenuElement;  