import React, { Component } from 'react';
import './App.css';
import GoodsComp from './GoodsComp/GoodsComp';
import SelectAllComp from './SelectAllComp/SelectAllComp';
import OtherElementsComp from './OtherElementsComp/OtherElementsComp';
import DinamicMenu from './DinamicMenu/DinamicMenu';

class App extends Component {
  state = {
    data: [
      { id:"Epiphone", title: "Epiphone SG Faded (G-400)", desc: "", price: "16 500 ₽",image: "image1.jpg", howOld:"7 лет, 5 месяцев назад", viewCount:"0 / 293", city:"Владивосток"},
      { id:"Behringer", title: "Behringer V-Amp", desc: "", price: "4 000 ₽",image: "image2.png", howOld:"7 лет, 7 месяцев назад", viewCount:"0 / 191", city:"Владивосток"},
      { id:"Lenovo", title: "Lenovo s-100", desc: "\"7\", 1,3 ГГц, ОЗУ 1024 Мб, диск 250 Гб, WiFi, аккумулятор на 5 ч.", price: "10 000 ₽",image: "", howOld:"7 лет, 7 месяцев назад", viewCount:"0 / 52", city:"Владивосток"},
      { id:"LenovoNew", title: "Lenovo s-100", desc: "как новый", price: "12 000 ₽",image: "", howOld:"7 лет, 7 месяцев назад", viewCount:"0 / 112", city:"Владивосток"},
      { id:"iPad", title: "iPhone 3gs, iPad 1, Nokia, Lenovo", desc: "б/у", price: "7 500 ₽",image: "", howOld:"7 лет, 7 месяцев назад", viewCount:"0 / 154", city:"Владивосток"},
      { id:"iPhoneGood", title: "iPhone 3gs 16gb в хорошем состоянии", desc: "Оригинал, б/у", price: "7 500 ₽",image: "", howOld:"7 лет, 7 месяцев назад", viewCount:"0 / 45", city:"Владивосток"}
    ],
    active: false,
    active: false,
    active: false,
    active: false,
    active: false,
    active: false,
    selectMenu: false
    }
    //changes button to select all 
    changeCompHandler = () => {
    const setActiveState = this.state.selectMenu;
    this.setState({selectMenu: !setActiveState});
    console.log(this.state.selectMenu);
    }



    elementToggleActiveHandler = (id) => {
          const doesShows = this.state[id+1];
          this.setState({[id+1]: !doesShows})
          console.log(this.state)
          }
          
    allActiveToggleHandler = () => {
            const allActive = this.state[1];
          this.setState({[1]:!allActive, [2]:!allActive, [3]:!allActive,[4]:!allActive, [5]:!allActive, [6]:!allActive  })
          console.log(this.state[1],this.state[2],this.state[3])
          
          }
          
    cancelButtonHandler = () => {
      this.changeCompHandler()
      this.setState({[1]:false, [2]:false, [3]:false,[4]:false, [5]:false, [6]:false})  
    }    


render () {

  const items = this.state.data.map((item, id) =>
  <GoodsComp
  isSelectable={this.state.selectMenu}
  clicked={() => this.elementToggleActiveHandler(id)}
  selectAll={this.state[id+1]}
  id={item.id}
  key={item.id}
  title={item.title}
  desc={item.desc}
  price={item.price}
  image={item.image}
  howOld={item.howOld}
  viewCount={item.viewCount}  
  city={item.city}
  />
  )

    let menu
  if (this.state[1] === true || this.state[2] === true || this.state[3] === true || this.state[4] === true || this.state[5] === true || this.state[6] === true) {
    menu =  (
      <DinamicMenu/>
    )
}

  return (
    <div className="App">
      <OtherElementsComp></OtherElementsComp>
      <SelectAllComp changeState={this.state.selectMenu} clicked={this.changeCompHandler} selectAll={this.allActiveToggleHandler} cancelButton={this.cancelButtonHandler} >Действия над объявлениями</SelectAllComp>
      {items}
      {menu}
    </div>
  );
}
}
export default App;
