import React from 'react'
import classes from './InlineMenu.module.css'


const InlineMenu = (props) => {

let listItem = [classes.listElement]
if(props.activeStyle) {
    listItem.push(classes.selectedElement)
}

    return (      
        <div className={classes.container}>
            <div className={listItem.join(' ')}>
                <div className={classes.title}>{props.title}</div>
                <div className={classes.counterGroup}> 
                    <div className={classes.counter}>{props.counter}</div><i className={classes.arrow}/>
                </div>

            </div>
            
        </div>
        
    )    
};

export default InlineMenu;  