import React from 'react'
import classes from './SelectAllComp.module.css'

const SelectAllComp = (props) => {

let transformToSelectAll = null;

if (props.changeState) {
    transformToSelectAll = (
        <div className={classes.container}>
            <label>
                <input style={{marginRight:"8px"}} type='checkbox' onClick={props.selectAll}></input> 
                Выделить все
            </label>    
            <button onClick={props.cancelButton}>Отмена</button> 
        </div>
    )
}
else {  
    transformToSelectAll = (
        <div  className={classes.singleButton}>
            <i/><button onClick={props.clicked}>Действия над объявлениями</button> 
        </div>
    )
}


    return (      
        <div className={classes.selectAll}>
        {transformToSelectAll}
        </div>
    )    
};

export default SelectAllComp;  